@import "../iconfont/navIcon.css"; //引入字体图标



/* 多级菜单 */
.syind_menu {
  width: 100%;
}
/* 第一级菜单 */
.syind_menu .syind_menu_ul{
  /*  border-top: 1px solid #2e3841;*/
}
.syind_menu .syind_menu_ul a{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.syind_menu .syind_menu_ul > li > a{
  width: 100%;
  height: 42px;
  line-height: 42px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: relative;
  padding-left: 6px;
  cursor: pointer;
  transition: all .2s;
  -moz-transition: all .2s;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  display: flex;
  align-items: center;
}
.syind_menu .syind_menu_ul  > li > a:after {
  display: none;
  content: "";
  width: 3px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

/* 悬浮和选中样式 */
/*.syind_menu .syind_menu_ul  > li > a.active:after,*/
.syind_menu .syind_menu_ul  > li > a:hover:after,
.syind_menu .syind_menu_ul  > li:hover > a:after{
  display: block;
}

.syind_menu .syind_menu_ul > li > a .group_icon {
  flex: 0 0 auto;
  width: 26px;
  margin-right: 2px;
  font-size: 18px;
  line-height: 10px;
  text-align: center;
}
.syind_menu .syind_menu_ul > li > a .group_title {
  flex: 1 1 auto;
}
.syind_menu .syind_menu_ul > li > a .group_arr {
  flex: 0 0 auto;
  width: 20px;
  text-align: center;
  font-size: 10px;
  transition: all .3s;
}
.syind_menu .syind_menu_ul > li > a .group_arr:before {
  content: '';
  display: block;
  width: 0px;
  height: 0px;
  transition: all .3s;
  transform: rotate(45deg);
}

/* 打开一级菜单样式 */
.syind_menu .syind_menu_ul  > li.open > a:after,
.syind_menu .syind_menu_ul  > li.open > a:after{
  display: block;
}
.syind_menu .syind_menu_ul > li.open > a .group_arr:before{
  transform: rotate(135deg);
}


/*二级*/
.syind_menu .syind_menu_ul .sub-menu {
  height: auto;
  box-sizing: border-box;
  display: none;
  padding:10px 0;
}
.syind_menu .syind_menu_ul .sub-menu > li a{
  width: 100%;
  box-sizing: border-box;
  cursor: pointer;
  padding-left: 34px;
  line-height: 34px;
  position: relative;
  transition: all .2s;
  -moz-transition: all .2s;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  display: flex;
  align-items: center;
}
.syind_menu .syind_menu_ul .sub-menu > li a .group_title{
  flex: 1 1 auto;
}
.syind_menu .syind_menu_ul .sub-menu > li a .group_arr{
  flex: 0 0 auto;
  width: 22px;
  text-align: center;
  position: relative;
}
.syind_menu .syind_menu_ul .sub-menu > li a .group_arr:before,
.syind_menu .syind_menu_ul .sub-menu > li a .group_arr:after{
  content: '';
  display: block;
  width: 12px;
  height: 1px;
  /*top: 2px;*/
  left: 0;
}
.syind_menu .syind_menu_ul .sub-menu > li a .group_arr:after{
  transform: rotate(90deg);
  transition: all .4s;
}
.syind_menu .syind_menu_ul .sub-menu > li.open a .group_arr:after{
  transform: rotate(0deg);
}
/* 三级菜单 */
.syind_menu .syind_menu_ul .sub-menu > li > .sub-menu{
  padding: 8px 8px 8px 35px;
}
.syind_menu .syind_menu_ul .sub-menu > li > .sub-menu > li a{
  height: 26px;
  line-height: 26px;
  border-radius: 13px;
  margin: 3px 0;
  padding-left: 13px;
}
.syind_menu .syind_menu_ul .sub-menu > li > .sub-menu > li a:before{
  content: '';
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  margin-right: 6px;
}


/* 展开箭头 */
.syind_menu .syind_menu_ul li a .group_arr{
  display:none;
}
.syind_menu .syind_menu_ul li.hasarr > a > .group_arr{
  display:block;
}


/* 默认展开菜单 */
.syind_menu .syind_menu_ul li.open > ul{
  display: block;
}
/* 多级菜单 end */






















